.header {
    &__logo {
        flex-shrink: 0;
        img {
            height: 150px;
            width: auto;
            flex-shrink: 0;
        }
    }
    &__inner {
        &-top {
            padding: 10px 0;
            border-bottom: 1px solid var(--iconColor);
        }
        &-bottom {
            box-shadow: var(--headerShadow);
        }
    }
    &__nav {
        &-top {
            flex-direction: column;
            align-items: flex-end;
            justify-content: center;
            flex-grow: 1;
            margin-left: 25px;
            font-weight: 600;
            a {
                display: flex;
                align-items: center;
                margin-bottom: 20px;
                i {
                    flex-shrink: 0;
                    margin-right: 10px;
                }
            }
        }
        &-bottom {
            font-weight: 600;
            li {
                margin-right: 25px;
            }
            .dropdown li {
                margin: 0;
            }
        }
    }
}
ul.header__nav-bottom {
    padding: 20px 0;
}
  